.bold {
  font-weight: bold;
}

.widget {
  margin-bottom: 25px;
  /* taken from https://css-tricks.com/examples/GradientBorder/ */
  border-bottom: 3px solid black;
  background: orange;
  padding: 2rem 4rem;
  position: relative;
}

.not-active {
  font-family: "Montserrat Subrayada", sans-serif;
  color: #ffffff;
  padding: 0.5rem;
  font-size: 2rem;
  font-weight: bold;
}

.light-overlay {
  position: absolute;
  height: auto;
  width: auto;
  z-index: 2;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 5px;
}

.cpu-widget {
  margin-top: 20px;
}

.cpu,
.mem {
  position: relative;
  text-align: center;
}

.cpu-text,
.mem-text {
  position: absolute;
  transform: translate(-45%, -50%);
  top: 50%;
  left: 50%;
  font-size: 30px;
}

.canvas-wrapper {
  position: relative;
}
